import { Injectable } from '@angular/core';
import { Store, State} from "@ngrx/store";
import { getStore } from 'src/app/_reducer/user.reducer';
import { User } from '../_model/user';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  login : boolean = false;
  user : User;
  data: User[];


  constructor(private _store:Store<getStore>) {
    this._store.select(item => item).subscribe(item =>{
      this.login = false;
      this.data = item.dataUser;
      if(this.data.length > 0){
        if(this.data[0].username === 'admin@gmail.com' && this.data[0].password === 'V1502_cv'){
          this.login = true;
        }
      }
    })
  }

  public isAuthenticated(): boolean {
    return this.login;
  }
}
