import { Component, OnInit } from '@angular/core';

import { CommonService } from '../../service/common.service';
import { CheckLoginService } from '../../service/check-login.service';
import { Store, State} from "@ngrx/store";
import { User } from '../../_model/user';
import { FormControl } from '@angular/forms';

import * as userLogins from '../../_action/userActions';
import { Router, NavigationEnd  } from '@angular/router';
import { getStore } from 'src/app/_reducer/user.reducer';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {

  constructor(private commonService: CommonService,
              private checkLoginService : CheckLoginService, 
              private _storeUser: Store<getStore>,
              private router: Router) {}

  lang = new FormControl('FR');
  login: boolean = false;
  data: any;
  userCurrent: User[];
  menu: any[] = [];
  cover_image: any[]=[];
  image_active: any;
  image_index: number = 0;
  loginText: Object = {
    "EN": "Log in",
    "FR": "Se connecter"
  }
  logoutText: Object = {
    "EN": "Log out",
    "FR": "Se déconnecter"
  }

  routerActiveLink: string;

  
  getData(){
    this.commonService.getData('common.json')
      .subscribe(data =>{
        this.data = data;
        this.handleData();
      });
  }

  handleData(){
      this.cover_image = this.data.infos.cover_image[this.lang.value];
      this.image_active = this.cover_image[0];
      this.menu = this.data.menu[this.lang.value]
  }

  // For the nav cover image
  currentSlide(n: number):void {
    this.image_index = n;
    this.image_active = this.cover_image[n];
  }
  // For the nav cover image
  plusSlides(n: number):void {
    this.image_index +=n;
    if(this.image_index >= this.cover_image.length) this.image_index = 0;
    if(this.image_index < 0) this.image_index = this.cover_image.length-1;
    this.currentSlide(this.image_index);
  }

  // change language
  changeLang(){
    this.commonService.changeLang(this.lang.value);
    this.handleData();
  }
  

  // For mobile vevrsion: menu
  checkMenu: boolean = false;
  openMenu() : void {
    this.checkMenu = true;
  } 
  closeMenu(): void {
    this.checkMenu = false;
  }


  logout():void {
    this.checkLoginService.logout();
    this.router.navigate(['/home']);
    this.closeMenu();
  }


  ngOnInit(): void {
    this.getData();
    this.commonService.changeLang(this.lang.value);

    this._storeUser.select(item => item).subscribe(item =>{
      this.login = false;
      this.userCurrent = item.dataUser;
      if(this.userCurrent.length > 0){
        if(this.userCurrent[0].username === 'admin@gmail.com' && this.userCurrent[0].password === 'V1502_cv'){
          this.login = true;
        }
      }
    })

    this.router.events.subscribe(event => {
      if (event instanceof NavigationEnd ) {
        this.routerActiveLink = event.url;
      }
    });
  }

}
