import { Component, OnInit } from '@angular/core';

import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { CommonService } from '../../service/common.service';
import { Store} from "@ngrx/store";
import { getStore } from 'src/app/_reducer/user.reducer';

@Component({
  selector: 'app-experience',
  templateUrl: './experience.component.html',
  styleUrls: ['./experience.component.css']
})
export class ExperienceComponent implements OnInit {

  constructor(
    private route: ActivatedRoute,
    private commonService: CommonService,
    private location: Location,
    private _storeLang: Store<getStore>
  ) { }

  data: any;
  lang: string = 'FR';
  experience: any;

  key: Object = {
    "FR": {
      "des": "Description",
      "date" : "Date",
      "tech" : "Technologie",
      "mission": "Mission",
      "link": "Le lien",
      "goback": "Retourner"

    },
    "EN": {
      "des": "Description",
      "date" : "Date",
      "tech" : "Technology",
      "mission": "Mission",
      "link": "Link",
      "goback": "Go backk"
    }

  }


  ngOnInit(): void {
    this._storeLang.select(item => item).subscribe(item => {
      this.lang = item.lang[0];
      this.handleData();
    });
    this.getData();
  }


  getData(): void {
    this.commonService.getData('experiences.json').subscribe(item => {
      this.data = item;
      this.handleData();
    });
  }

  handleData(): void {
    const id = +this.route.snapshot.paramMap.get('id');
    if(this.data != undefined){
      this.experience = this.data[this.lang].find(item => item.id == id)
    }
  }

  goback(): void {
    this.location.back();
  }

}
