import { NgModule } from '@angular/core';
import { Routes, RouterModule, CanActivate } from '@angular/router';
import { AuthGuardService as AuthGuard } from './auth/auth-guard.service';

import { HomeComponent } from './component/home/home.component';
import { ProjectsComponent } from './component/projects/projects.component';
import { ExperiencesComponent } from './component/experiences/experiences.component';
import { SkillsComponent } from './component/skills/skills.component';
import { ContactsComponent } from './component/contacts/contacts.component';
import { LoginComponent } from './component/login/login.component';
import { ProjectComponent } from './component/project/project.component';
import { ExperienceComponent } from './component/experience/experience.component';

// , canActivate: [AuthGuard]
const routes: Routes = [
  {path: 'home', component: HomeComponent},
  {path: 'projects', component: ProjectsComponent, canActivate: [AuthGuard]},
  {path: 'experiences', component: ExperiencesComponent, canActivate: [AuthGuard]},
  {path: 'skills', component: SkillsComponent, canActivate: [AuthGuard]},
  {path: 'contacts', component: ContactsComponent, canActivate: [AuthGuard]},
  {path: 'login', component: LoginComponent},
  {path: 'project/:id', component: ProjectComponent, canActivate: [AuthGuard]},
  {path: 'experience/:id', component: ExperienceComponent, canActivate: [AuthGuard]},
  {path: '', redirectTo: '/home', pathMatch: 'full'},
  {path: '**', redirectTo: '/home', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
