import { Component, OnInit } from '@angular/core';
import { CommonService } from '../../service/common.service';

import { Store} from "@ngrx/store";
import { getStore } from 'src/app/_reducer/user.reducer';

@Component({
  selector: 'app-contacts',
  templateUrl: './contacts.component.html',
  styleUrls: ['./contacts.component.css']
})
export class ContactsComponent implements OnInit {

  constructor(
    private commonService: CommonService,
    private _storeLang: Store<getStore>
  ) { }

  lang: string;
  data: any;
  contact: any;

  ngOnInit(): void {
    this.getData();
    this._storeLang.select(item => item).subscribe(item => {
      this.lang = item.lang[0];
      if(this.data != undefined){
        this.handleData();
      }
    });
  }

  getData(): void {
    this.commonService.getData('contact.json')
      .subscribe(data =>{
        this.data = data;
        this.handleData();
      });
  }

  handleData(){
    this.contact = this.data[this.lang];
  }

}
