import { Injectable } from '@angular/core';
import { Store, State} from "@ngrx/store";
import { getStore } from 'src/app/_reducer/user.reducer';
import { User } from '../_model/user';
import * as userLogins from '../_action/userActions';

@Injectable({
  providedIn: 'root'
})
export class CheckLoginService {

  constructor(private _store:Store<getStore>) { }

  _check: boolean = false;
  data: User[];
  user: User;

  checkLogin():boolean {
    this._store.select(item => item).subscribe(item =>{
      this._check = false;
      this.data = item.dataUser;
      if(this.data.length > 0){
        if(this.data[0].username === 'admin@gmail.com' && this.data[0].password === 'V1502_cv'){
          this._check = true;
        }
      }
    })
    return this._check;
  }

  getInfos(): User {
    this._store.select(item => item).subscribe(item =>{
      this.data = item.dataUser;
      if(this.data.length > 0){
        this.user = this.data[0];
      }
    })

    return this.user;
  }

  logout() : void {
    const user: User = this.getInfos();
    this._store.dispatch(new userLogins.CheckoutAction({
      username:  user.username,
      password: user.password
    }));
  }

  login():void {
    this._store.dispatch(new userLogins.CheckLoginAction({
      username:  'admin@gmail.com',
      password: 'V1502_cv'
    }));
  }
}
