import { Component, OnInit } from '@angular/core';


import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { CommonService } from '../../service/common.service';
import { Store} from "@ngrx/store";
import { getStore } from 'src/app/_reducer/user.reducer';

@Component({
  selector: 'app-project',
  templateUrl: './project.component.html',
  styleUrls: ['./project.component.css']
})
export class ProjectComponent implements OnInit {

  constructor(
    private route: ActivatedRoute,
    private commonService: CommonService,
    private location: Location,
    private _storeLang: Store<getStore>
  ) { }
  
  data: any;
  lang: string = 'FR';
  project: any;

  key: Object = {
    "FR": {
      "des": "Description",
      "date" : "Date",
      "tech" : "Technologies",
      "func": "Fonctions",
      "link": "Source code",
      "goback": "Retourner"

    },
    "EN": {
      "des": "Description",
      "date" : "Date",
      "tech" : "Technologies",
      "func": "Functions",
      "link": "Source code",
      "goback": "Go backk"
    }

  }

  ngOnInit(): void {
    this._storeLang.select(item => item).subscribe(item => {
      this.lang = item.lang[0];
      this.handleData();
    });
    this.getData();
  }

  getData(): void {
    this.commonService.getData('projects.json').subscribe(item => {
      this.data = item;
      this.handleData();
    });
  }

  handleData(): void {
    const id = +this.route.snapshot.paramMap.get('id');
    if(this.data != undefined){
      this.project = this.data[this.lang].find(item => item.id == id)
    }
  }

  goback(): void {
    this.location.back();
  }

}
