import { Component, OnInit } from '@angular/core';

import { Store} from "@ngrx/store";
import { getStore } from 'src/app/_reducer/user.reducer';

@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.css']
})
export class FooterComponent implements OnInit {

  constructor(private _storeLang: Store<getStore>) { }

  lang : string;
  text: Object = {
    "EN": "Worked with a lot of passion.",
    "FR": "Travaillé avec beaucoup de passion."
  }

  ngOnInit(): void {
    this._storeLang.select(item => item).subscribe(item => {
      this.lang = item.lang[0];
    });
  }

}
