import * as userLogins from '../_action/userActions';
import { User } from '../_model/user';

export interface getStore {
    dataUser : User[],
    lang: string[]
}

export function userReducer (state = [], action:userLogins.Actions): User[] {
    switch(action.type) {
        case userLogins.ActionType.CHECK_LOGIN : {
            const user:User = action.payload;
            return [...state,user];
        }
        case userLogins.ActionType.CHECKOUT : {
            return state.filter(item => {
                return item.username != action.payload.username;
            })
        }
        default: {
            return state;
        }
    }
}