import { Component, OnInit } from '@angular/core';
import { CommonService } from '../../service/common.service';


import { Store} from "@ngrx/store";
import { getStore } from 'src/app/_reducer/user.reducer';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  constructor(private commonService: CommonService,
          private _storeLang: Store<getStore>) { }
  lang: string;
  data: any;
  description: string;
  character: any[] = [];
  evaluate: any[] = [];

  ngOnInit(): void {
    this.getData();
    this._storeLang.select(item => item).subscribe(item => {
      this.lang = item.lang[0];
      if(this.data != undefined){
        this.handleData();
      }
    });
  }

  getData(): void {
    this.commonService.getData('home.json')
      .subscribe(data =>{
        this.data = data;
        this.handleData();
      });
  }

  handleData(){
    this.description = this.data.description[this.lang];
    this.character = this.data.character[this.lang];
    this.evaluate = this.data.evaluate[this.lang];
  }

}
