import { Component, OnInit } from '@angular/core';

import { CommonService } from '../../service/common.service';


import { Store} from "@ngrx/store";
import { getStore } from 'src/app/_reducer/user.reducer';

@Component({
  selector: 'app-projects',
  templateUrl: './projects.component.html',
  styleUrls: ['./projects.component.css']
})
export class ProjectsComponent implements OnInit {

  constructor(private commonService: CommonService,
              private _storeLang: Store<getStore>) { }

  lang: string;
  data: any;
  projects: any[]=[];

  ngOnInit(): void {
    this.getData();
    this._storeLang.select(item => item).subscribe(item => {
      this.lang = item.lang[0];
      if(this.data != undefined){
        this.handleData();
      }
    });
  }

  getData(): void {
    this.commonService.getData('projects.json')
      .subscribe(data =>{
        this.data = data;
        this.handleData();
      });
  }

  handleData(){
    this.projects = this.data[this.lang];
  }

}
